import Vue from 'vue';
import Router from 'vue-router';
import DetailForward from '@/components/blacklist/DetailForward';
import NotFound from '@/components/NotFound';
import LandingServiceInfo from '@/components/screens/LandingNew';
var routes = [{
  path: '/',
  name: 'LandingServiceInfo',
  component: LandingServiceInfo,
  props: true
}, {
  path: '/danh-sach-ho-so/:index',
  name: 'Landing',
  component: function component() {
    return import(
    /* webpackChunkName: "Landing" */
    '@/components/blacklist/Landing.vue');
  },
  props: true,
  meta: {
    requiresAuth: false
  }
}, {
  path: '/danh-sach-ho-so/:index/tiep-nhan-ho-so/:id/:formCode',
  name: 'TiepNhanHoSoDetail',
  component: function component() {
    return import(
    /* webpackChunkName: "TiepNhanHoSoDetail" */
    '@/components/blacklist/TiepNhanHoSoDetail.vue');
  },
  props: true,
  meta: {
    requiresAuth: false
  }
}, {
  path: '/danh-sach-ho-so/:index/bo-sung-ho-so/:id',
  name: 'HoanThienBoSungHoSoDetail',
  component: function component() {
    return import(
    /* webpackChunkName: "HoanThienBoSungHoSoDetail" */
    '@/components/blacklist/HoanThienBoSungHoSoDetail.vue');
  },
  props: true,
  meta: {
    requiresAuth: false
  }
}, {
  path: '/danh-sach-ho-so/:index/ho-so/:id/:formCode',
  name: 'DetailForward',
  component: DetailForward,
  props: true
}, {
  path: '*',
  name: 'NotFound',
  component: NotFound
}];
Vue.use(Router);
var router = new Router({
  // mode: 'history',
  routes: routes
});
export default router;